@import "../common/mixins";
@import "../common/variable";

.get-touch {
  padding-top: r(80);
  padding-bottom: r(80);
  &__container {}
  &__wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    grid-gap: r(20);
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    @include max-tablet {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      grid-gap: r(35);
    }
  }
  &__picture {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    max-width: r(687);
    font-size: 0;
    picture {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      font-size: 0;
    }
    img {
      width: 100%;
    }
  }
  &__right {
    width: 100%;
    max-width: r(685);
  }
  &__title {
    color: $colorOrange;
  }
  &__content {
    max-width: r(490);
    margin-top: r(35);
  }
  &__form {
    margin-top: r(45);
  }
}

.form-contact {
  &__row {
    &:not(:last-child) {
      margin-bottom: r(22);
    }
    input, textarea {
      display: block;
      width: 100%;
      height: r(57);
      padding: r(15) r(25);
      font-size: r(20);
      color: $textColor;
      resize: none;
      background-color: #f7f7f7;
      border: none;
      &::-webkit-input-placeholder {
        color: currentColor;
      }
      &::-moz-placeholder {
        color: currentColor;
      }
      &:-ms-input-placeholder {
        color: currentColor;
      }
      &::-ms-input-placeholder {
        color: currentColor;
      }
      &::placeholder {
        color: currentColor;
      }
    }
    textarea {
      height: r(150);
    }
  }
  &__action {
    margin-top: r(45);
  }
}